import React, { Component } from 'react';
import Page from '../../components/Page';
import MathJax from '../../components/MathJax';

const data = `
${`矢印キー、ユーザー、ユーザーＩＤ、ユーザーインタフェース、ユーザーテスト、
ユニキャストアドレス、ユニークユーザー、ユニットテスト、イニバーサルデザイン、ユビキタス、要求定義、要件定義、
ユーザービリティテスト、ユースケース、ユニキャスト、予約語、
`.split('\n').join('').split('、').map(v=>v.trim()).filter(v=>v!='').sort().join('、')}
`;

export default function({ pageInfo }) {
  return (
    <Page
      title={pageInfo.title}
      page={pageInfo.page}
      maxPage={pageInfo.maxPage}
      category={pageInfo.category}
      data={data}
      pageDivide={false}
    />
  )
}
